import { useEffect, useState } from "react";
import { supabase } from "../lib/supabaseClient";

/**
 * âœ… å…¨å±€é€šç”¨ Hookï¼šSupabase ç™»å½• / è§’è‰² / ç™»å‡º ç®¡ç†
 *
 * ç”¨æ³•ï¼š
 * const { user, profile, loading, signOut } = useSupabaseAuth();
 *
 * å¯åœ¨ä»»æ„é¡µé¢åˆ¤æ–­ï¼š
 *   if (profile?.role === 'admin') { ... }
 */
export default function useSupabaseAuth() {
  const [user, setUser] = useState(null);
  const [profile, setProfile] = useState(null);
  const [loading, setLoading] = useState(true);

  // åŠ è½½å½“å‰ Session
  useEffect(() => {
    const init = async () => {
      const { data } = await supabase.auth.getSession();
      const sessionUser = data?.session?.user || null;
      setUser(sessionUser);

      if (sessionUser) {
        await loadProfile(sessionUser.id);
      } else {
        setProfile(null);
      }
      setLoading(false);
    };

    init();

    // ç›‘å¬ç™»å½•çŠ¶æ€å˜åŒ–
    const { data: listener } = supabase.auth.onAuthStateChange((_event, session) => {
      const currentUser = session?.user || null;
      setUser(currentUser);
      if (currentUser) loadProfile(currentUser.id);
      else setProfile(null);
    });

    return () => listener.subscription.unsubscribe();
  }, []);

  // ä» profiles è¡¨åŠ è½½è§’è‰²ç­‰ä¿¡æ¯
  async function loadProfile(userId) {
    const { data, error } = await supabase
      .from("profiles")
      .select("email, full_name, role, avatar_url, created_at")
      .eq("id", userId)
      .single();

    if (!error && data) {
      setProfile(data);
    } else {
      console.warn("âš ï¸ æ— æ³•åŠ è½½ç”¨æˆ·èµ„æ–™ï¼š", error?.message);
    }
  }

  // ç™»å‡ºå‡½æ•°
  async function signOut() {
    await supabase.auth.signOut();
    setUser(null);
    setProfile(null);
  }

  return { user, profile, loading, signOut };
}
